import uniPopup from '../../components/common/uni-popup/uni-popup.vue'
import uParse from '@/components/gaoyia-parse/parse.vue'
import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';
export default {
	data() {
		return {
			showPopup: false,
			id: '',
			labelList: [],
			params: {}
		}
	},
	components: {
		uniPopup,
		uParse

	},
	onLoad(option) {
		this.id = option.id || '';
		this.id && this.getDetailData();
	},
	methods: {
		getDetailData() {
			uniRequest.post('/hotel/getbyidhotelinfo', {
				id: this.id
			}).then(res => {
				if(res.data.code == '000000') {
					this.params = {...res.data.data}
					this.formatLabel(this.params.label)
				} else {
					uni.showToast({
						title: res.data.message
					})
				}
			})
		},
		formatLabel(val) {
			this.labelList = val.split('ï¼Œ')
		},
		handleShowPopup() {
			this.showPopup = true;
		},
		hidePopup() {
			this.showPopup = false;
		},
		handleCall(number) {
			uni.makePhoneCall({
				phoneNumber: number
			});
		}
	}
	// computed: {
	// 	labelList() {
	// 		return this.params.label.split(',')
	// 	}
	// }
}
